.App {
  font-family: sans-serif;
  text-align: center;
}

#userProfiles {
  font-family: Comfortaa-Bold, Helvetica, sans-serif;
  text-align: center;
  border-collapse: collapse;
  width: 100%;
}

#userProfiles td,
#userProfiles th {
  border: 1px solid #ddd;
  padding: 8px;
}

#userProfiles tr:nth-child(even) {
  background-color: #f2f2f2;
}

#userProfiles tr:hover {
  background-color: #ddd;
}

#userProfiles th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #229b7d;
  color: white;
}
